import axios from "axios";

const initializeApp = () => {
    // Configurar la base URL automticamente segn el entorno
    const baseURL = process.env.REACT_APP_NODE_ENV === 'production' 
        ? process.env.REACT_APP_BASE_URL_PROD 
        : process.env.REACT_APP_BASE_URL_DEV;

    axios.defaults.baseURL = baseURL;

    if (process.env.REACT_APP_NODE_ENV === 'development') {
        // Cdigo para desarrollo
        console.log("Modo desarrollo activo");
    } else {
        // Cdigo para produccin
        console.log = () => {}; // Deshabilitar logs en produccin
        // Inicializar analytics, etc.
    }
}

export default initializeApp;
