// src/app/apiSlice.js
import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import axios from 'axios';
import { getAuthToken } from '../../app/authUtils';

export const fetchEntities = createAsyncThunk(
  'entities/fetchEntities',
  async ({ entityName, page, limit, fields, sort, sortOrder, }, { rejectWithValue }) => {
    try {
      const token = getAuthToken();
      if (!token) throw new Error('Authentication token is missing');

      const response = await axios.get(`/api/${entityName}`, {
        headers: {
          Authorization: token,
        },
        params: {
          page,
          limit,
          fields,
          sort,
          sortOrder,
        },
      });
      console.log(response.data);
      return response.data;
    } catch (error) {
      return rejectWithValue(error.response?.data || error.message);
    }
  }
);

export const fetchEntityById = createAsyncThunk(
  'entities/fetchEntityById',
  async ({ entityName, id }, { rejectWithValue }) => {
    try {
      const token = getAuthToken();
      if (!token) throw new Error('Authentication token is missing');

      const response = await axios.get(`/api/${entityName}/${id}`, {
        headers: {
          Authorization: token,
        },
      });
      return response.data;
    } catch (error) {
      return rejectWithValue(error.response?.data || error.message);
    }
  }
);

export const createEntity = createAsyncThunk(
  'entities/createEntity',
  async ({ entityName, newEntity }, { rejectWithValue }) => {
    try {
      const token = getAuthToken();
      if (!token) throw new Error('Authentication token is missing');

      const response = await axios.post(`/api/${entityName}`, newEntity, {
        headers: {
          Authorization: token,
        },
      });
      return response.data;
    } catch (error) {
      return rejectWithValue(error.response?.data || error.message);
    }
  }
);

export const updateEntity = createAsyncThunk(
  'entities/updateEntity',
  async ({ entityName, id, updatedEntity }, { rejectWithValue }) => {
    try {
      const token = getAuthToken();
      if (!token) throw new Error('Authentication token is missing');

      const response = await axios.put(`/api/${entityName}/${id}`, updatedEntity, {
        headers: {
          Authorization: token,
        },
      });
      return response.data;
    } catch (error) {
      return rejectWithValue(error.response?.data || error.message);
    }
  }
);

export const deleteEntity = createAsyncThunk(
  'entities/deleteEntity',
  async ({ entityName, id }, { rejectWithValue }) => {
    try {
      const token = getAuthToken();
      if (!token) throw new Error('Authentication token is missing');

      await axios.delete(`/api/${entityName}/${id}`, {
        headers: {
          Authorization: token,
        },
      });
      return id;
    } catch (error) {
      return rejectWithValue(error.response?.data || error.message);
    }
  }
);

export const fetchExecuteSQL = createAsyncThunk(
  'entities/fetchExecuteSQL',
  async ({ fields, pivotTable, joinTable, joinCondition, whereCondition, orderBy, page = 1, limit = 50, noPagination = false }, { rejectWithValue }) => {
    try {
      const token = getAuthToken();
      if (!token) throw new Error('Authentication token is missing');

      const response = await axios.get('/api/execute-sql', {
        headers: {
          Authorization: token,
        },
        params: {
          fields,
          pivotTable,
          joinTable,
          joinCondition,
          whereCondition,
          orderBy,
          page,
          limit,
          noPagination,
        },
      });

      // Extraer la informacin de paginacin
      const { totalPages, totalRecords, firstRecordIndex, lastRecordIndex } = response.data;

      return {
        data: response.data, // Suponiendo que la respuesta contiene los registros
        pagination: {
          totalPages,
          currentPage: noPagination ? 'Todos' : parseInt(page, 10),
          totalRecords,
          firstRecordIndex,
          lastRecordIndex,
        },
      };
    } catch (error) {
      return rejectWithValue(error.response?.data || error.message);
    }
  }
);

const apiSlice = createSlice({
  name: 'entities',
  initialState: {
    entities: [],
    status: 'idle',
    error: null,
    entity: null,
    executeSQLResult: null,  // Agregar un estado para los resultados de execute-sql
    pagination: null,         // Agregar un estado para la paginacin
  },
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(fetchEntities.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchEntities.fulfilled, (state, action) => {
        state.status = 'succeeded';
        state.entities = action.payload;
      })
      .addCase(fetchEntities.rejected, (state, action) => {
        state.status = 'failed';
        state.error = action.payload;
      })
      .addCase(fetchEntityById.fulfilled, (state, action) => {
        state.entity = action.payload;
      })
      .addCase(createEntity.fulfilled, (state, action) => {
        state.entities.push(action.payload);
      })
      .addCase(deleteEntity.fulfilled, (state, action) => {
        state.entities = state.entities.filter((entity) => entity.id !== action.payload);
      })
      .addCase(updateEntity.fulfilled, (state, action) => {
        const index = state.entities.findIndex((entity) => entity.id === action.payload.id);
        if (index !== -1) {
          state.entities[index] = action.payload;
        }
      })
      .addCase(fetchExecuteSQL.fulfilled, (state, action) => {
        state.executeSQLResult = action.payload.data; // Guardar los resultados de la consulta SQL
        state.pagination = action.payload.pagination;   // Guardar la informacin de paginacin
      });
  },
});

export default apiSlice.reducer;